.wallet-adapter-button {
    color: #fff;
    display: flex;
    align-items: center;
    font-family: 'Inter', 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 16px;
    font-weight: 600;
    height: 50px;
    line-height: 50px;
}

[dir] .wallet-adapter-button {
    background-color: #404144;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    padding: 0 20px;
}

[dir] .wallet-adapter-button-trigger {
    background-color: #4e44ce;
}

.wallet-adapter-button:not([disabled]):focus-visible {
    outline-color: white;
}

[dir] .wallet-adapter-button:not([disabled]):hover {
    background-image: linear-gradient(rgba(0, 0, 0, 0.15) 0 0);
}

.wallet-adapter-button[disabled] {
    color: #999;
}

[dir] .wallet-adapter-button[disabled] {
    background: #404144;
    cursor: not-allowed;
}

.wallet-adapter-button-end-icon,
.wallet-adapter-button-start-icon,
.wallet-adapter-button-end-icon img,
.wallet-adapter-button-start-icon img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
}

[dir=ltr] .wallet-adapter-button-end-icon {
    margin-left: 8px;
}

[dir=rtl] .wallet-adapter-button-end-icon {
    margin-right: 8px;
}

[dir=ltr] .wallet-adapter-button-start-icon {
    margin-right: 8px;
}

[dir=rtl] .wallet-adapter-button-start-icon {
    margin-left: 8px;
}

.wallet-adapter-collapse {
    width: 100%;
}

.wallet-adapter-dropdown {
    position: relative;
    display: inline-block;
}

.wallet-adapter-dropdown-list {
    position: absolute;
    z-index: 99;
    display: grid;
    grid-template-rows: 1fr;
    grid-row-gap: 10px;
    top: 100%;
    list-style: none;
    opacity: 0;
    visibility: hidden;
    transition: opacity 200ms ease, transform 200ms ease, visibility 200ms;
}

[dir] .wallet-adapter-dropdown-list {
    padding: 10px;
    margin: 0;
    background: #2c2d30;
    border-radius: 10px;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.6);
}

[dir=ltr] .wallet-adapter-dropdown-list {
    right: 0;
}

[dir=rtl] .wallet-adapter-dropdown-list {
    left: 0;
}

.wallet-adapter-dropdown-list-active {
    opacity: 1;
    visibility: visible;
}

[dir] .wallet-adapter-dropdown-list-active {
    transform: translateY(10px);
}

.wallet-adapter-dropdown-list-item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    outline: none;
    white-space: nowrap;
    box-sizing: border-box;
    width: 100%;
    font-size: 14px;
    font-weight: 600;
    height: 37px;
    color: white;
}

[dir] .wallet-adapter-dropdown-list-item {
    background: #404144;
    border: none;
    cursor: pointer;
    padding: 0 20px;
    border-radius: 6px;
}

[dir] .wallet-adapter-dropdown-list-item:not([disabled]):hover {
    background-image: linear-gradient(rgba(0, 0, 0, 0.15) 0 0);
}

.wallet-adapter-modal-collapse-button svg {
    align-self: center;
    fill: #999;
}

.wallet-adapter-modal-collapse-button.wallet-adapter-modal-collapse-button-active svg {
    transition: transform ease-in 150ms;
